import React from "react"
import "./styles.css"

/***************** ICONS ******************/
import {ReactComponent as ReactLogo} from "./img/react-brands.svg"
import {ReactComponent as HtmlLogo} from "./img/html5-brands.svg"
import {ReactComponent as CssLogo} from "./img/css3-brands.svg"
import {ReactComponent as JavaLogo} from "./img/java-brands.svg"
import {ReactComponent as JavascriptLogo} from "./img/js-brands.svg"

import {ReactComponent as GitLogo} from "./img/github-brands.svg"
import {ReactComponent as LinkedinLogo} from "./img/linkedin-brands.svg"
import {ReactComponent as MailLogo} from "./img/at-solid.svg"


function Home(props){
    return(
        <div className="home" ref={props.myRef} >
            <h3>Kristofer Odehammar</h3>
            <h4>Front end developer</h4>
            <a href="http://hotheda.github.io">
                <GitLogo className="logolink"/>
            </a>
            <a href="http://www.linkedin.com/in/kristofer-odehammar-868a60177/">
                <LinkedinLogo  className="logolink"/>
            </a>
            <a href="mailto:kristofer@odehammar.com" title="Mail me">
                <MailLogo className="logolink"/>
            </a>
            <p>Projects coded in</p>
            <p>C++ Java C# Javascript React HTML CSS</p>
            <HtmlLogo className="logoicon"/>
            <ReactLogo className="logoicon"/>
            <CssLogo className="logoicon"/>
            <JavaLogo className="logoicon"/>
            <JavascriptLogo className="logoicon"/>
        </div>
    )
}

export default Home