import React from "react"

function Project(props){
    return(
        <div>
            <h3>{props.myProject.name}</h3>
            <p>{props.myProject.info}</p>
            <img className="proj-img" src={ props.myProject.img1 } alt="Project pic"/>
            <img className="proj-img" src={ props.myProject.img2 } alt="Project pic"/>
            <hr/>
        </div>
    )
}

export default Project