import React from "react"

import {ReactComponent as GitLogo} from "./img/github-brands.svg"
import {ReactComponent as LinkedinLogo} from "./img/linkedin-brands.svg"
import {ReactComponent as MailLogo} from "./img/at-solid.svg"


function Contact(props){
    return(
        <div className="contact" ref={props.myRef} >
            <h3>TODO: Contact me:</h3>
            <p>Contact me here</p>
            <p>GitHub Email LinkedIn</p>
            <a href="http://hotheda.github.io">
                <GitLogo className="logolink" />
            </a>
            <a href="http://www.linkedin.com/in/kristofer-odehammar-868a60177/">
                <LinkedinLogo className="logolink" />
            </a>
            <a href="mailto:kristofer@odehammar.com">
                <MailLogo  className="logolink"/>
            </a>
            <hr/>
        </div>
    )
}

export default Contact