import React from "react"
import Project from "./Project"

/****** import images ******/


function Projects(props){
    const myProjects=[{
                name: "Schemakollen",
                info: "Mobile app for your work schedual that tracks you salary",
                img1: require("./img/projects/nexus-schema1.png"),
                img2: require("./img/projects/nexus-schema2.png")
            },{
                name: "Fältskytte",
                info: "Timed commands when shooting",
                img1: require("./img/projects/nexus-falt1.png"),
                img2: require("./img/projects/nexus-falt2.png")
            }
        ];

        /***** Mappar min function till 2 components *****/
        const myProj = myProjects.map((myProjects)=> <Project key={myProjects.name} myProject={myProjects} /> )

    return(
        <div className="projects" ref={props.myRef} >
            <h2 style={{textDecoration: "underline"}} >Projects</h2>
            <h3>Here are some of my previous projects</h3>
            <br/>
            {myProj}
        </div>
    )
}

export default Projects